*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --text-input-font-size: 1.5rem;
  --label-font-size: 1.5rem;
  --speed-input-font-size: var(--label-font-size);
  --button-font-size: var(--label-font-size);
  --text-input-focus-outline-color: #4084bf;
}

body {
  width: 90vw;
  height: 50vh;
  margin: 10px auto;
}

.text-input {
  font-size: var(--text-input-font-size);
  border: none;
  outline: 1px solid #000;
  width: 100%;
  height: 100%;
  margin-bottom: 5px;
  padding: 10px;
}

.text-input:focus {
  outline: 2px solid var(--text-input-focus-outline-color);
}

.speed-label {
  font-size: var(--label-font-size);
}

.speed-input {
  font-size: var(--speed-input-font-size);
}

.button {
  font-size: var(--button-font-size);
}
